input.input-custom-onchange::-webkit-input-placeholder {
    padding-top: 25px !important;
    padding-left: 12px !important;
}
input.input-custom-onchange:-ms-input-placeholder {
    padding-top: 25px !important;
    padding-left: 12px !important;
}
input.input-custom-onchange::placeholder {
    padding-top: 25px !important;
    padding-left: 12px !important;
}
.input-custom-focus {
    /*border-color: #57ad57;*/
    border-color: #11939A !important;
    -webkit-box-shadow: none !important;
            box-shadow: none !important;
}
div.relative {
    position: relative;
}
.form-group-custom > label {
    position: absolute;
    top: 6px;
    left: 21px;
    -webkit-transition: all .5s ease;
    -o-transition: all .5s ease;
    transition: all .5s ease;
    opacity: 0;
    font-size: 11px;
    color: #717171;
    z-index: 1;
}
.show-label > input {
    padding-top: 24px;
    -webkit-transition: all .3s ease!important;
    -o-transition: all .3s ease!important;
    transition: all .3s ease!important;
}
.show-label > label {
    top: 2px;
    opacity: 1;
    color: #57ad57
}


