main div section#banner-become-bro {
    position: relative;
    background-color: #000
}

main div section#banner-become-bro:before {
    content: "";
    display: block;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url(../../assets/images/banner-5.png);
    width: 100%;
    height: 100%;
    opacity: .8;
    position: absolute;
    left: 0;
    top: 0
}

main div section#banner-become-bro h2 {
    color: #fff;
    font-size: 4rem
}

main div section#banner-become-bro h2:first-child {
    margin-top: 190px
}

@media (max-width: 1200px) {
    main div section#banner-become-bro h2 {
        font-size:3.5rem
    }
}

@media (max-width: 992px) {
    main div section#banner-become-bro h2:first-child {
        margin-top:0
    }

    main div section#banner-become-bro h2 {
        font-size: 2.5rem
    }

    main div section#banner-become-bro h2:last-child {
        margin-bottom: 25px
    }
}

main div section#banner-become-bro .card.register .card-body .card-title {
    line-height: 2.7rem
}

main div section#banner-become-bro .card.register .card-body form .or-container {
    margin-bottom: 15px
}

main div section#banner-become-bro .card.register .card-body form .or-container span {
    background-color: #fff
}

main div section#banner-become-bro .card.register .card-body form textarea {
    resize: none;
    height: 200px;
    margin: 10px 0 35px
}

main div section#banner-become-bro .card.register .card-body form textarea::-webkit-input-placeholder {
    padding-top: 70px;
    text-align: center
}

main div section#banner-become-bro .card.register .card-body form textarea::-moz-placeholder {
    padding-top: 70px;
    text-align: center
}

main div section#banner-become-bro .card.register .card-body form textarea:-ms-input-placeholder {
    padding-top: 70px;
    text-align: center
}

main div section#banner-become-bro .card.register .card-body form textarea:-moz-placeholder {
    padding-top: 70px;
    text-align: center
}

main div section#why-become-ae .container .row .ae-card {
    padding: 0;
    -webkit-box-shadow: none;
            box-shadow: none;
    margin-top: 75px
}

main div section#why-become-ae .container .row .ae-card .card-body .card-image {
    margin-bottom: 20px
}

main div section#why-become-ae .container .row .ae-card .card-body .card-title {
    font-size: 1.8rem;
    margin-bottom: 10px
}



