main section#policy .container .row .content-tree .card,main section#policy .container .row .content-tree .card .card-body {
    padding: 0 15px
}

main section#policy .container .row .content-tree .card .card-body ul {
    list-style: none inside;
    margin: 0 -27px;
    padding-left: 0
}

main section#policy .container .row .content-tree .card .card-body ul li a {
    padding: 15px;
    display: block;
    font-weight: 700;
    color: #252525
}

main section#policy .container .row .content-tree .card .card-body ul li a:hover,main section#policy .container .row .content-tree .card .card-body ul li.active a {
    color: #4dc4df
}

main section#policy .container .row .content-tree .card .card-body ul li:not(:last-child) a {
    border-bottom: 1px solid rgba(0,0,0,.125)
}

@media (max-width: 992px) {
    main section#policy .container .row .content-tree {
        margin-bottom:25px
    }
}

main section#policy .container .row .content .content-banner {
    height: 300px;
    position: relative;
    margin-bottom: 20px
}

main section#policy .container .row .content .content-banner:before {
    content: "";
    display: block;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url(../../assets/images/banner-6.png);
    width: 100%;
    height: 100%;
    opacity: .52;
    position: absolute;
    z-index: 0;
    left: 0;
    top: 0
}

main section#policy .container .row .content .content-banner:after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-color: #000;
    position: absolute;
    z-index: -1;
    left: 0;
    top: 0
}

main section#policy .container .row .content .content-banner h2 {
    color: #fff;
    font-size: 3rem;
    position: relative;
    padding-top: 120px;
    text-align: center
}

main section#policy .container .row .content .content-banner .btn-group {
    position: absolute;
    bottom: 15px;
    right: 15px;
    z-index: 999
}

main section#policy .container .row .content .content-banner .btn-group button {
    color: #fff;
    padding: 10px 25px;
    border: 1px solid #fff;
    background-color: transparent
}

main section#policy .container .row .content .content-banner .btn-group button img {
    height: 10px;
    margin-left: 20px;
    vertical-align: baseline
}

main section#policy .container .row .content .content-title {
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 24px
}

main section#policy .container .row .content .content-detail {
    margin-bottom: 60px
}
.customViewer {
    border: 1px solid rgba(0,0,0,.125);
}
.customViewer > canvas {
    width: 100% !important;
    height: auto !important;
}
.customViewer > .mgrpdf-navigation  {
    background-color: rgba(50, 50, 50, 0.71) !important;
    padding: 11px;
}
.customViewer > .mgrpdf-navigation > i {
    font-size: 30px;
}


