main section#intro h1 {
    font-size: 30px;
    margin-bottom: 15px
}

main section#intro h2 {
    font-size: 20px;
    margin-bottom: 15px
}

main section#intro img {
    max-width: 100%
}

@media (max-width: 992px) {
    main section#intro img {
        margin-bottom:20px
    }
}

main section#intro .card {
    margin-bottom: 20px
}

main section#intro .card .card-body .card-title {
    font-weight: 700;
    margin-bottom: 30px
}

main section#intro .card .card-body .card-text {
    color: #888
}

main section#intro .or-container {
    margin-bottom: 30px
}

main section#reason {
    padding: 0 0 100px;
    background-color: #f8f8f8;
    position: relative
}

@media (max-width: 992px) {
    main section#reason {
        padding-top:100px
    }
}

@media (max-width: 768px) {
    main section#reason {
        padding:50px 0
    }
}

main section#reason:before {
    content: "";
    display: block;
    width: 100%;
    height: 200px;
    background-color: #f8f8f8;
    position: absolute;
    z-index: -1;
    top: -102px
}

@media (max-width: 992px) {
    main section#reason:before {
        display:none
    }
}

main section#reason>.container>h2 {
    font-size: 3rem;
    /*color: #888;*/
    position: relative;
    display: inline;
    top: 25px
}

main section#reason>.container>h2:after {
    content: "";
    display: block;
    width: 28px;
    height: 41px;
    background-image: url(assets/icons/arrow-down.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    position: absolute;
    right: -43px;
    top: 0
}

@media (max-width: 375px) {
    main section#reason>.container>h2:after {
        display:none
    }
}

@media (max-width: 992px) {
    main section#reason>.container>h2 {
        top:unset
    }
}

main section#reason>.container .card {
    min-height: 320px;
    margin-top: 100px;
    padding-top: 75px;
    position: relative
}

main section#reason>.container .card .card-body {
    padding-top: 30px;
    padding-bottom: 70px
}

main section#reason>.container .card .card-body .card-img {
    width: 130px;
    height: 130px;
    border: 15px solid #f8f8f8;
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    position: absolute;
    top: -65px;
    left: 0;
    right: 0;
    margin: 0 auto
}

main section#become-ae:after,main section#become-ae:before {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    height: 100%;
    width: 100%;
    content: ""
}

main section#reason>.container .card .card-body .card-title {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 15px
}

main section#reason>.container .card .card-body .card-text {
    font-size: 1.4rem;
    text-align: center
}

@media (max-width: 1200px) {
    main section#reason>.container .card {
        min-height:350px
    }
}

@media (max-width: 768px) {
    main section#reason>.container .card .card-body {
        padding-bottom:20px
    }

    main section#reason>.container .card {
        min-height: 300px
    }
}

main section#become-ae {
    position: relative
}

main section#become-ae:after {
    z-index: -1;
    background-image: url(assets/images/banner-2.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    opacity: .5
}

main section#become-ae:before {
    background-color: #06697f;
    z-index: -999
}

main section#become-ae>.container {
    padding: 180px 15px 150px
}

main section#become-ae>.container h3 {
    color: #fff;
    font-size: 4rem;
    margin-bottom: 4.1rem
}

@media (max-width: 768px) {
    main section#become-ae>.container h3 {
        font-size:3rem
    }
}

@media (max-width: 500px) {
    main section#become-ae>.container h3 {
        font-size:2.5rem
    }
}

main section#become-ae>.container a.ae-button {
    font-size: 2rem;
    padding: 15px 25px
}

main section#become-ae>.container a.ae-button:after {
    content: "";
    display: inline-block;
    vertical-align: middle;
    margin-left: 20px;
    height: 30px;
    width: 30px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url(assets/icons/sign-out.png)
}

main section#become-ae>.container a.ae-button:hover {
    color: #fff!important;
    border-color: #fff!important;
    background-color: transparent!important
}

@media (max-width: 500px) {
    main section#become-ae>.container a.ae-button {
        font-size:1.4rem
    }
}



